/* Modal */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 50%;
    text-align: center;
  }
  
  .modal-close {
    position: absolute; /* Posiciona o botão dentro do modal */
    top: calc(14% + 10px); /* Adiciona um espaçamento dinâmico */ /* Distância do topo */
    right: 27%; /* Distância da direita */
    background-color: #ff5722; /* Cor de fundo */
    color: white; /* Cor do texto */
    border: none; /* Remove bordas */
    cursor: pointer; /* Cursor de clique */
    padding: 5px; /* Tamanho reduzido */
    font-size: 16px; /* Tamanho da fonte */
    border-radius: 100%; /* Torna o botão circular */
    width: 30px; /* Largura do botão */
    height: 30px; /* Altura do botão */
    display: flex; /* Centraliza o ícone no botão */
    justify-content: center;
    align-items: center;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2); /* Sombra sutil */
    transition: background-color 0.3s ease;
  }
  .modal.funcionarios .modal-close {
    top: 20%; /* Ajuste conforme necessário */
    right: 30%;
  }
  .modal-close:hover {
    background-color: #e64a19;
  }

  .modal-buttons {
    /* Botões no rodapé do modal */
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
  }
  
  .modal-buttons button {
    padding: 10px 20px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .modal-buttons button:first-child {
    background-color: #4caf50; /* Verde para salvar */
    color: white;
  }
  
  .modal-buttons button:nth-child(2) {
    background-color: #f44336; /* Vermelho para excluir */
    color: white;
  }
  
  .modal-buttons button:last-child {
    background-color: #2196f3; /* Azul para editar */
    color: white;
  }
  
  .modal-buttons button:hover {
    opacity: 0.9;
  }
  