/* =======================
   1. ESTILOS GERAIS
   ======================= */
   .usuarios-container {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  

  
  /* =======================
     2. BOTÕES GERAIS
     ======================= */
  .btn-cadastrar {
    margin-bottom: 20px;
    padding: 10px 20px;
    background-color: #ffc107;
    color: #003366;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .btn-cadastrar:hover {
    background-color: #e0a800;
    color: white;
  }
  
  .modal-buttons {
    /* Botões no rodapé do modal */
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
  }
  
  .modal-buttons button {
    padding: 10px 20px;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .modal-buttons button:first-child {
    background-color: #4caf50; /* Verde para salvar */
    color: white;
  }
  
  .modal-buttons button:nth-child(2) {
    background-color: #f44336; /* Vermelho para excluir */
    color: white;
  }
  
  .modal-buttons button:last-child {
    background-color: #2196f3; /* Azul para editar */
    color: white;
  }
  
  .modal-buttons button:hover {
    opacity: 0.9;
  }
  
  .buttons-removeObra button {
    /* Botão para remover obra */
    padding: 10px 15px;
    background-color: #003366;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
  }
  
  /* =======================
     3. TABELA DE FUNCIONÁRIOS
     ======================= */
  .tabela-usuarios-container {
    /* Container da tabela com rolagem */
    max-height: 500px;
    overflow-y: auto;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  
  .tabela-usuarios {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    
    margin: 0 auto;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: block;
    overflow-x: auto;
    
  }
  
  .tabela-usuarios th {
    /* Cabeçalhos da tabela */
    background-color: #003366;
    color: #ffffff;
    font-weight: bold;
    text-transform: uppercase;
    padding: 10px;
    text-align: center;
    font-size: 14px;
  }
  
  .tabela-usuarios td {
    border: none;
    padding: 8px;
    text-align: center;
  }
  
  
  
  .tabela-usuarios tbody tr:nth-child(odd) {
    background-color: #ffffff;
  }
  
  .tabela-usuarios tbody tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .tabela-usuarios tbody tr {
    color: #333;
  }
  
  .tabela-usuarios tbody tr:hover {
    background-color: #ddd;
    color: #003366;
  }
  
  .tabela-usuarios th,
  .tabela-usuarios td {
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  /* =======================
     4. MODAL
     ======================= */
  .modal {
    /* Fundo do modal */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.3s ease;
  }
  
  .modal-content {
    /* Conteúdo do modal */
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    width: 80%;
    max-width: 700px;
    max-height: 800px;
    display: flex;
    flex-direction: column;
    
  }
  
  .modal-body {
    /* Corpo do modal (com duas colunas) */
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .modal-left {
    /* Coluna esquerda do modal */
    flex: 1;
    padding-right: 20px;
    border-right: 1px solid #ddd;
  }
  
  .modal-left img {
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin-bottom: 10px;
  }
  
  .modal-right {
    /* Coluna direita do modal */
    flex: 1;
    padding-left: 20px;
  }
  
  .modal-right ul {
    /* Lista na coluna direita */
    list-style: none;
    padding: 0;
    margin: 0;
    max-height: 500px;
    overflow-y: auto;
  }
  
  .modal-right ul li {
    /* Itens da lista */
    background-color: #f9f9f9;
    margin-bottom: 5px;
    padding: 8px;
    border-radius: 5px;
  }
  
  .modal-right h3 {
    margin-bottom: 10px;
    color: #003366;
  }
  
  /* =======================
     5. CAMPOS DE FORMULÁRIO
     ======================= */
  .modal .modal-body .form-group {
    margin-bottom: 1px;
  }
  
  .modal .modal-body .form-group input,
  .modal .modal-body .form-group textarea {
    /* Estilo dos campos de entrada */
    width: 70%;
    padding: 12px;
    border-radius: 8px;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    color: #333;
    font-family: Arial, sans-serif;
    box-sizing: border-box;

  }
  
  .modal .modal-body .form-group input:focus,
  .modal .modal-body .form-group textarea:focus {
    outline: none;
    border-color: #40c9ff;
    background-color: #e9ecef;
  }
  
  .modal .modal-body .form-group input::placeholder,
  .modal .modal-body .form-group textarea::placeholder {
    color: #999;
  }
  